// 辅助函数：查找最近路径点
function findNearestPointIndex(path: number[][], position: any) {
     // 参数校验
     if (!Array.isArray(path) || path.length === 0 || !position?.lng || !position?.lat) {
        return 0; // 基本参数异常时默认返回第一个点
    }

    let minDiff = Infinity;
    let nearestIndex = 0;

    for (let i = 0; i < path.length; i++) {
        const [lng, lat] = path[i];
        // 计算经纬度差值的绝对值之和（不转换实际距离）
        const diff = Math.abs(lng - position.lng) + Math.abs(lat - position.lat);

        if (diff < minDiff) {
            minDiff = diff;
            nearestIndex = i;
        }
    }

    return nearestIndex;
}

